import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-guest',
})
export class OrderGuest {

    @Element() root: HTMLElement;
    @Prop() orderWithoutLogin: string = "";

    componentDidLoad(){
        this.root.style.display = "block"
    }


    render() {
        return[
            <div class="uk-padding">
                <div class="uk-text-center" style={{maxWidth: "500px", margin: "auto"}}>
                    <p class="ks-text-decorated uk-text-bold" style={{fontSize: "28px"}}>{this.orderWithoutLogin}</p>

                    <slot />
                </div>
            </div>
        ];
    }
}